import React, { Component } from "react";
import { connect } from "react-redux";
import { compose } from "redux";
// import { Redirect } from "react-router-dom";

import RoleUpdateForm from "./updateForm";

import notification from "../../../../components/notification";
import { Spin } from "antd";

import LayoutWrapper from "../../../../components/utility/layoutWrapper";
import PageHeader from "../../../../components/utility/pageHeader";
import IntlMessages from "../../../../components/utility/intlMessages";
import Button from "../../../../components/uielements/button";
import actions from "../../../../redux/employee/role/actions";
import { Box } from "../../../App/app.style";

const redirectTo = "/dashboard/setup/roles"

class UpdateRole extends Component {
  // Get All data diawal
  componentDidMount() {
    const { editRole } = this.props;
    const { id } = this.props.match.params;
    editRole(id);
  }

  componentDidUpdate() {
    // Display error notif
    if (this.props.role.error) {
      notification("error", this.props.role.error);
      this.props.clearError();
    }

    if (this.props.role.success) {
      notification("success", this.props.role.success);
      this.props.clearSuccess();
      this.props.history.replace(redirectTo)
    }
  }

  handleSubmit = value => {
    const { updateRole } = this.props;
    const { id } = this.props.match.params;

    // Uppercasing supplier type code
    // value.job_title_code = value.job_title_code.toUpperCase();
    updateRole(id, value);
  };

  render() {
    // console.log(this.props.role);
    return (
      <LayoutWrapper>
        <Box>
          <PageHeader title={<IntlMessages id="roles.update.title" />}
            style={{ margin: 0 }}
          >
            <Button onClick={() => this.props.history.replace(redirectTo)}>
              <IntlMessages id="button.back" />
            </Button>
          </PageHeader>

          {this.props.role.loading &&
            !this.props.role.loadingSubmit ? (
              <div className="spinStyle">
                <Spin size="large" />
              </div>
            ) : (
              <RoleUpdateForm
                auth={this.props.auth}
                submit={this.handleSubmit}
                role={this.props.role}
              />
            )}
        </Box>
      </LayoutWrapper>
    );
  }
}

const mapStateToProps = state => {
  return {
    auth: state.auth,
    role: state.role
  };
};

export default compose(
  connect(
    mapStateToProps,
    actions
  )
)(UpdateRole);
