const actions = {
  getRoles: (page, search, filter) => ({
    type: 'GET_ROLES_REQUEST',
    payload: {
      page: page,
      search: search,
      filter: filter
    }
  }),
  createRole: () => ({
    type: 'CREATE_ROLE_REQUEST'
  }),
  storeRole: data => ({
    type: 'STORE_ROLE_REQUEST',
    payload: { body: data }
  }),
  editRole: id_role => ({
    type: 'EDIT_ROLE_REQUEST',
    payload: { id_role: id_role }
  }),
  updateRole: (id_role, data) => ({
    type: 'UPDATE_ROLE_REQUEST',
    payload: {
      id_role: id_role,
      body: data
    }
  }),
  showRole: id_role => ({
    type: 'SHOW_ROLE_REQUEST',
    payload: { id_role: id_role }
  }),
  deactivateRole: id_role => ({
    type: 'DEACTIVATE_ROLE_REQUEST',
    payload: { id_role }
  }),
  activateRole: id_role => ({
    type: 'ACTIVATE_ROLE_REQUEST',
    payload: { id_role }
  }),
  clearSuccess: () => ({
    type: "CLEAR_SUCCESS"
  }),
  clearError: () => ({
    type: "CLEAR_ERROR"
  }),
  clearErrorField: () => ({
    type: "CLEAR_ERROR_FIELD"
  })
};

export default actions;
