import React, { Component } from "react";

import IntlMessages from "../../../../components/utility/intlMessages";
import Button from "../../../../components/uielements/button";
import { Form, Input, Row, Col, Tabs, Checkbox } from "antd";
import getLanguage from "../../../App/MyLanguange"
//const { Option } = Select;
const { TextArea } = Input;
const TabPane = Tabs.TabPane;
const CheckboxGroup = Checkbox.Group;

class RoleUpdateForm extends Component {
  state = {
    id_permission : [],
    check: 2
  }

  componentDidMount = () => {
    const { role } = this.props.role;
    //console.log(role)
    if(this.state.check === 2 && 
     // prevProps.role.role !== this.props.role.role &&
      this.props.role.role !== null
    ){
      const { setFieldsValue } = this.props.form;
      setFieldsValue({
        role_name: role.role_name,
        description: role.description
      })
      
      this.setState({
        id_permission: role.id_permission,
        check: 1
      })
    }
  }
  
  handleSubmit = e => {
    e.preventDefault();
    this.props.form.validateFieldsAndScroll((err, values) => {
      if (!err) {
        values.id_permission = this.state.id_permission
        this.props.submit(values);
      }
    });
  };

  handleChangeCheck = e => {
    const { permission_list } = this.props.role;
    // console.log(e)
    let check = e.filter((x) => !this.state.id_permission.includes(x))
    let uncheck = this.state.id_permission.filter((x) => !e.includes(x))

    // console.log(check)
    // console.log(uncheck)

    if(check.length > 0){
      let permission = permission_list.find((x) => x.value === check[0]) 
      let id_permission = [... new Set([...this.state.id_permission,...e, ...permission.permission_checks])]
      // console.log(permission.permission_checks);
      // console.log(id_permission)

      // Cek All apa tidak
      let label_all = permission_list.find((x)  => x.label==="All" && x.permission_checks.includes(check[0]));
      if(label_all){
        let label_all_permission = label_all.permission_checks.filter((x) => !id_permission.includes(x));
        if(label_all_permission.length === 0){
          id_permission.push(label_all.value);
        };
      }
      
      this.setState({
        id_permission
      })
    }
    else{
      let permission = permission_list.find((x) => x.value === uncheck[0]) 
      let id_permission = e.filter((x)  => !permission.permission_unchecks.includes(x));
      // console.log(permission.permission_unchecks);
      // console.log(id_permission)
      this.setState({
        id_permission
      })
    }
  }

  render() {
    // console.log(this.props.role.role);
    const { getFieldDecorator } = this.props.form;
    const { role } = this.props;
    const { loading, permissions, raptor_module } = role;
    const data = role.role ? role.role : [];

    // const used = data.is_used;
    // const error = role.errorField ? role.errorField : [];
     //console.log(used);

    const formItemLayout = {
      labelCol: {
        xs: { span: 24 },
        sm: { span: 24 }
      },
      wrapperCol: {
        xs: { span: 24 },
        sm: { span: 24 }
      }
    };
    const formTextArea = {
      labelCol: {
        xs: { span: 24 },
        sm: { span: 24 }
      },
      wrapperCol: {
        xs: { span: 24 },
        sm: { span: 24 }
      }
    };
    const tailFormItemLayout = {
      wrapperCol: {
        xs: {
          span: 24,
          offset: 0
        },
        sm: {
          span: 24,
          offset: 0
        }
      }
    };

    let setup_permissions = [];
    let purchase_permissions = [];
    let sales_permissions = [];
    let raptor_permissions = [];
    let inventory_permissions = [];
    let finance_permissions = [];
    let gl_permissions = [];
    let asset_permissions = [];
    let promotion_permissions = [];
    let report_permissions = []


    //Setup permission
    permissions.map((permission)  =>{
      if(permission.parent_module === 8){
        let permissions_lang = []
        
        permission.permissions.map((data) => {
          permissions_lang.push({
            ...data,
            label: getLanguage({id: data.label})
          })
        })

        setup_permissions.push({
          ...permission,
          label : getLanguage({id: permission.permission_label}),
          value : permission.id_permission,
          permissions: permissions_lang
        })
      }
    })

    // Purchase permission
    permissions.map((permission)  =>{
      if(permission.parent_module === 1){
        let permissions_lang = []
        
        permission.permissions.map((data) => {
          permissions_lang.push({
            ...data,
            label: getLanguage({id: data.label})
          })
        })

        purchase_permissions.push({
          ...permission,
          label : getLanguage({id: permission.permission_label}),
          value : permission.id_permission,
          permissions: permissions_lang
        })
      }
    })

    // Sales permission
    permissions.map((permission)  =>{
      if(permission.parent_module === 2){
        let permissions_lang = []
        
        permission.permissions.map((data) => {
          permissions_lang.push({
            ...data,
            label: getLanguage({id: data.label})
          })
        })

        sales_permissions.push({
          ...permission,
          label : getLanguage({id: permission.permission_label}),
          value : permission.id_permission,
          permissions: permissions_lang
        })
      }
    })

    // Raptor permission
    permissions.map((permission)  =>{
      if(permission.parent_module === 10){
        let permissions_lang = []
        
        permission.permissions.map((data) => {
          permissions_lang.push({
            ...data,
            label: getLanguage({id: data.label})
          })
        })

        raptor_permissions.push({
          ...permission,
          label : getLanguage({id: permission.permission_label}),
          value : permission.id_permission,
          permissions: permissions_lang
        })
      }
    })

    // Inventory permission
    permissions.map((permission)  =>{
      if(permission.parent_module === 3){
        let permissions_lang = []
        
        permission.permissions.map((data) => {
          permissions_lang.push({
            ...data,
            label: getLanguage({id: data.label})
          })
        })

        inventory_permissions.push({
          ...permission,
          label : getLanguage({id: permission.permission_label}),
          value : permission.id_permission,
          permissions: permissions_lang
        })
      }
    })

    // Finance permission
    permissions.map((permission)  =>{
      if(permission.parent_module === 4){
        let permissions_lang = []
        
        permission.permissions.map((data) => {
          permissions_lang.push({
            ...data,
            label: getLanguage({id: data.label})
          })
        })

        finance_permissions.push({
          ...permission,
          label : getLanguage({id: permission.permission_label}),
          value : permission.id_permission,
          permissions: permissions_lang
        })
      }
    })

    // GL permission
    permissions.map((permission)  =>{
      if(permission.parent_module === 5){
        let permissions_lang = []
        
        permission.permissions.map((data) => {
          permissions_lang.push({
            ...data,
            label: getLanguage({id: data.label})
          })
        })

        gl_permissions.push({
          ...permission,
          label : getLanguage({id: permission.permission_label}),
          value : permission.id_permission,
          permissions: permissions_lang
        })
      }
    })

    // Asset permission
    permissions.map((permission)  =>{
      if(permission.parent_module === 6){
        let permissions_lang = []
        
        permission.permissions.map((data) => {
          permissions_lang.push({
            ...data,
            label: getLanguage({id: data.label})
          })
        })

        asset_permissions.push({
          ...permission,
          label : getLanguage({id: permission.permission_label}),
          value : permission.id_permission,
          permissions: permissions_lang
        })
      }
    })

    // Promotion permission
    permissions.map((permission)  =>{
      if(permission.parent_module === 7){
        let permissions_lang = []
        
        permission.permissions.map((data) => {
          permissions_lang.push({
            ...data,
            label: getLanguage({id: data.label})
          })
        })

        promotion_permissions.push({
          ...permission,
          label : getLanguage({id: permission.permission_label}),
          value : permission.id_permission,
          permissions: permissions_lang
        })
      }
    })

    // Report permission
    permissions.map((permission)  =>{
      if(permission.parent_module === 9){
        let permissions_lang = []
        
        permission.permissions.map((data) => {
          permissions_lang.push({
            ...data,
            label: getLanguage({id: data.label})
          })
        })

        report_permissions.push({
          ...permission,
          label : getLanguage({id: permission.permission_label}),
          value : permission.id_permission,
          permissions: permissions_lang
        })
      }
    })

    // console.log(report_permissions)

    return (
      <Form layout="vertical" onSubmit={this.handleSubmit}>
        <div className="isoDetailDataFlex" style={{ padding: "0" }}>
          <div className="isoColumn-2 isoFormGroupComp" style={{ padding: "0 15px 0 0" }}>
            <Form.Item
              {...formItemLayout}
              label={<IntlMessages id="roles.add.name" />}
            >
              {getFieldDecorator("role_name", {
                // initialValue: data.role_name,
                rules: [
                  {
                    required: true,
                    message: <IntlMessages id="required" />
                  },
                  {
                    max: 50,
                    message: <IntlMessages id="maxLength50" />
                  }
                ]
              })(
                <Input
                //   disabled={used ? true : false}
                />
              )}
            </Form.Item>

            <Form.Item
              {...formTextArea}
              label={<IntlMessages id="description" />}
            >
              {getFieldDecorator("description", {
                // initialValue: data.description
              })(<TextArea autosize={{ minRows: 2 }} />)}
            </Form.Item>
          </div>
        </div>
        <Tabs defaultActiveKey="1">
          <TabPane tab={<IntlMessages id="roles.tab.master" />} 
            key="1" 
            style={{ marginLeft:"25px" }}
          > 
            <br />
            {
              setup_permissions.map((data)  => {
                return (
                  <React.Fragment key={data.id_module} >
                    <Row>
                      <Col span={5}>
                        <strong style={{ fontSize:"16px" }}> {getLanguage({id: data.module_name})} </strong>
                      </Col>
                      {/* <br /> */}
                      <Col span={19}>
                        <CheckboxGroup
                          options={data.permissions}
                          value={this.state.id_permission}
                          onChange={this.handleChangeCheck}
                        />
                      </Col>
                    </Row>
                    <br />
                  </React.Fragment>
                )
              })
            }
          </TabPane>
          <TabPane tab={<IntlMessages id="roles.tab.purchase" />} 
            key="2"
          >  
            <br />
            {
              purchase_permissions.map((data)  => {
                return (
                  <React.Fragment key={data.id_module} >
                    <Row>
                      <Col span={5}>
                        <strong style={{ fontSize:"16px" }}> {getLanguage({id: data.module_name})} </strong>
                      </Col>
                      {/* <br /> */}
                      <Col span={19}>
                        <CheckboxGroup
                          options={data.permissions}
                          value={this.state.id_permission}
                          onChange={this.handleChangeCheck}
                        />
                      </Col>
                    </Row>
                    <br />
                  </React.Fragment>
                ) 
              })
            }
          </TabPane>
          <TabPane tab={<IntlMessages id="roles.tab.sales" />} 
            key="3"
          > 
            <br />
            {
              sales_permissions.map((data)  => {
                return (
                  <React.Fragment key={data.id_module} >
                    <Row>
                      <Col span={5}>
                        <strong style={{ fontSize:"16px" }}> {getLanguage({id: data.module_name})} </strong>
                      </Col>
                      {/* <br /> */}
                      <Col span={19}>
                        <CheckboxGroup
                          options={data.permissions}
                          value={this.state.id_permission}
                          onChange={this.handleChangeCheck}
                        />
                      </Col>
                    </Row>    
                    <br />
                  </React.Fragment>
                )
              })
            }
          </TabPane>
          {
            raptor_module === 1
            ?
              (
                <TabPane tab={<IntlMessages id="roles.tab.raptor" />} 
                  key="10"
                > 
                  <br />
                  {
                    raptor_permissions.map((data)  => {
                      return (
                        <React.Fragment key={data.id_module} >
                          <Row>
                            <Col span={5}>
                              <strong style={{ fontSize:"16px" }}> {getLanguage({id: data.module_name})} </strong>
                            </Col>
                            {/* <br /> */}
                            <Col span={19}>
                              <CheckboxGroup
                                options={data.permissions}
                                value={this.state.id_permission}
                                onChange={this.handleChangeCheck}
                              />
                            </Col>
                          </Row>    
                          <br />
                        </React.Fragment>
                      )
                    })
                  }
                </TabPane>
              )
            : null
          }
          <TabPane tab={<IntlMessages id="roles.tab.inventory" />} 
            key="4"
          > 
            <br />
            {
              inventory_permissions.map((data)  => {
                return (
                  <React.Fragment key={data.id_module} >
                    <Row>
                      <Col span={5}>
                        <strong style={{ fontSize:"16px" }}> {getLanguage({id: data.module_name})} </strong>
                      </Col>
                      {/* <br /> */}
                      <Col span={19}>
                        <CheckboxGroup
                          options={data.permissions}
                          value={this.state.id_permission}
                          onChange={this.handleChangeCheck}
                        />
                      </Col> 
                    </Row>   
                    <br />
                  </React.Fragment>
                )
              })
            }
          </TabPane>
          <TabPane tab={<IntlMessages id="roles.tab.finance" />} 
            key="5"
          > 
            <br />
            {
              finance_permissions.map((data)  => {
                return (
                  <React.Fragment key={data.id_module} >
                    <Row>
                      <Col span={5}>
                        <strong style={{ fontSize:"16px" }}> {getLanguage({id: data.module_name})} </strong>
                      </Col>
                      {/* <br /> */}
                      <Col span={19}>
                        <CheckboxGroup
                          options={data.permissions}
                          value={this.state.id_permission}
                          onChange={this.handleChangeCheck}
                        />
                      </Col>    
                    </Row>
                    <br />
                  </React.Fragment>
                )
              })
            }
          </TabPane>
          <TabPane tab={<IntlMessages id="roles.tab.generalLedger" />} 
            key="6"
          > 
            <br />
            {
              gl_permissions.map((data)  => {
                return (
                  <React.Fragment key={data.id_module} >
                    <Row>
                      <Col span={5}>
                        <strong style={{ fontSize:"16px" }}> {getLanguage({id: data.module_name})} </strong>
                      </Col>
                      {/* <br /> */}
                      <Col span={19}>
                        <CheckboxGroup
                          options={data.permissions}
                          value={this.state.id_permission}
                          onChange={this.handleChangeCheck}
                        />
                      </Col>    
                    </Row>
                    <br />
                  </React.Fragment>
                )
              })
            }
          </TabPane>
          <TabPane tab={<IntlMessages id="roles.tab.asset" />} 
            key="7"
          > 
            <br />
            {
              asset_permissions.map((data)  => {
                return (
                  <React.Fragment key={data.id_module} >
                    <Row>
                      <Col span={5}>
                        <strong style={{ fontSize:"16px" }}> {getLanguage({id: data.module_name})} </strong>
                      </Col>
                      {/* <br /> */}
                      <Col span={19}>
                        <CheckboxGroup
                          options={data.permissions}
                          value={this.state.id_permission}
                          onChange={this.handleChangeCheck}
                        />
                      </Col>    
                    </Row>
                    <br />
                  </React.Fragment>
                )
              })
            }
          </TabPane>
          <TabPane tab={<IntlMessages id="roles.tab.promotion" />} 
            key="8"
          > 
            <br />
            {
              promotion_permissions.map((data)  => {
                return (
                  <React.Fragment key={data.id_module} >
                    <Row>
                      <Col span={5}>
                        <strong style={{ fontSize:"16px" }}> {getLanguage({id: data.module_name})} </strong>
                      </Col>
                      {/* <br /> */}
                      <Col span={19}>
                        <CheckboxGroup
                          options={data.permissions}
                          value={this.state.id_permission}
                          onChange={this.handleChangeCheck}
                        />
                      </Col>    
                    </Row>
                    <br />
                  </React.Fragment>
                )
              })
            }
          </TabPane>
          <TabPane tab={<IntlMessages id="roles.tab.report" />} 
            key="9"
          > 
            <br />
            {
              report_permissions.map((data)  => {
                return (
                  <React.Fragment key={data.id_module} >
                    <Row>
                      <Col span={5}>
                        <strong style={{ fontSize:"16px" }}> {getLanguage({id: data.module_name})} </strong>
                      </Col>
                      {/* <br /> */}
                      <Col span={19}>
                        <CheckboxGroup
                          options={data.permissions}
                          value={this.state.id_permission}
                          onChange={this.handleChangeCheck}
                        />
                      </Col>    
                    </Row>
                    <br />
                  </React.Fragment>
                )
              })
            }
          </TabPane>
        </Tabs>

        <Form.Item {...tailFormItemLayout}>
          <Button
            type="primary"
            htmlType="submit"
            style={{ marginTop: "20px", marginLeft: "50px" }}
            loading={loading}
          >
            <IntlMessages id="button.submit" />
          </Button>
        </Form.Item>
      </Form>
    );
  }
}

const WrappedRoleUpdateForm = Form.create({
  name: "update_role"
})(RoleUpdateForm);

export default WrappedRoleUpdateForm;
